.loginContainer{
    width: 400px;
   
    background-color: rgb(33,33,33);
    display: flex;
    flex-direction: column;
    gap: 30px;
    justify-content: center;
    align-items: center;
    margin: auto;
}
.loginContainer input{
    padding: 10px 30px;
    border-radius: 2px;
    background-color: black;
    border: none;
    color: grey;
}
.loginContainer button{
    padding: 8px 90px;
    border-radius: 10px;
    background-color:rgb(0,89,255) ;
    border: none;
}
.loginContainer .link{
    background-color: inherit;
    color: inherit;
    padding-bottom: 20px;

}